<template>
  <div class="dashboard-container">
    <div class="dashboard-text">name: {{ name }}</div>
    <div v-if="showWeChat" class="modules">
      <a
        class="module"
        v-for="(m, i) in modules"
        :key="i"
        :href="m.link"
        target="_blank"
      >
        <i class="module-icon" :class="m.icon"></i>
        <span class="module-name">{{ m.name }}</span>
      </a>
    </div>
  </div>
</template>

<script>
import { mapGetters } from 'vuex'
import { getToken } from '@/utils/auth'

function getRealToken() {
  const token = getToken() || ''
  return token.replace('Bearer ', '')
}
export default {
  name: 'Dashboard',
  computed: {
    ...mapGetters(['name'])
  },
  data() {
    return {
      showWeChat: true,
      modules: [
        {
          name: '公众号管理',
          icon: '',
          link:
            process.env.VUE_APP_MODULE_URL_WECHAT +
            '/auth/login?_token=' +
            getRealToken()
        }
      ]
    }
  },
  created() {
    this.showWeChat = window.location.host !== 'oa.baobaohehu.com'
  }
}
</script>

<style lang="scss" scoped>
.dashboard {
  &-container {
    margin: 30px;
  }
  &-text {
    line-height: 46px;
    font-size: 30px;
  }
}
.modules {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  padding: 50px 30px;
  .module {
    display: block;
    margin: 20px;
    width: 72px;
    .module-icon {
      display: block;
      width: 72px;
      height: 72px;
      font-size: 72px;
      background: #eee;
      border: 1px solid #ccc;
      border-radius: 14px;
    }
    .module-name {
      display: block;
      overflow: hidden;
      margin-top: 10px;
      font-size: 12px;
      font-weight: 500;
      text-align: center;
      text-overflow: ellipsis;
      white-space: nowrap;
      color: #666;
      word-break: break-all;
    }
  }
}
</style>
